.custom-select {
    
    margin-top: 30px;
    width: 80%;
    margin-left: 10%;
    background-color: aqua;
   
}
select {   
    width: 100%;
    padding: 20px;
    background-color: #FDC00F;;
    border: 1px solid #000000; 
    font-size: 20px; 
}

select > option {   
    width: 100%;
    padding: 20px;
    font-size: 20px;
    padding: 10px;
}

/* Remove seta padr√£o do FireFox */
.custom-select select::-ms-expand {
    display: none;
}
/* Remove seta padr√£o do IE*/

@media screen and (max-width: 600px) {

    
    select {   
        width: 100%;
        padding: 20px;         
    }
    
   
}